<template>
  <div class="nav scale-1px">
    <div class="nav-item active">
      <a href="#/goods">商品</a>
    </div>
    <div class="nav-item">
      <a href="#/commonts">评价</a>
    </div>
    <div class="nav-item">
      <a href="#/seller">商家</a>
    </div>
  </div>
</template>
<script>
  export default {
    name:"nav"
  }
</script>
<style scoped>
  .nav{
    display: flex;
  }
  .scale-1px{
    position: relative;
    border: none;
  }
  .scale-1px::after{
    content: '';
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 1px;
    background-color: rgba(7,17,27,0.1);
    transform: scaleY(0.5);
    transform-origin: 0 0;
  }
  .nav-item{
    width: 100%;
    height: 40px;
    text-align: center;
    flex: 1;
    line-height: 40px;
  }
</style>



