<template>
  <!-- 商品内容区 -->
  <div class="content" ref="content">
    <ul>
      <li v-for="(item,goodIndex) in goods" class="menu-classify menu-classify-hook">
        <h3 class="title">{{item.name}}</h3>
        <ul>
          <li v-for="(food,foodIndex) in item.foods" class="food" aaa="food">
            <div class="food-icon-wrapper">
              <img v-bind:src="food.icon" alt="foodIcon" class="food-icon">
            </div>
            <div class="food-details">
              <h4 class="food-title">{{food.name}}</h4>
              <p class="description" v-if="food.description">{{food.description}}</p>
              <p class="sales-and-applause">
                <span class="sales-volume">月售{{food.sellCount}}份</span>
                <span class="applause-rate">好评率{{food.rating}}%</span>
              </p>
              价格及购买区域
              <div class="price">
                <p class="current-price"><span class="symbol">￥</span>{{food.price}}</p>
                <p v-if="food.oldPrice" class="old-price"><span class="symbol">￥</span>{{food.oldPrice}}</p>
                <!-- 购买按钮 -->
                <cartButton class="cartButton" v-bind="{food:food,goodIndex:goodIndex,foodIndex:foodIndex}"></cartButton>
              </div>
            </div>
            <span class="line"></span>
          </li>


        </ul>
      </li>
    </ul>
  </div>
</template>
<script>
  // const ERR_OK=0;
  import {mapState} from 'vuex';
  import BScroll from "better-scroll";
  import Vue from 'vue';
  import bus from './bus.js';
  import cartButton from "./cartButton/cartButton";
  export default {
    name:"content",
    components:{
      "cartButton":cartButton
    },
    data:function () {
      return {
        scrollY:0,
        itemTop:[],
      }
    },
    computed:{
      ...mapState(['goods']),
      currentIndex:function () {
        for (let i=0;i<this.itemTop.length;i++){
          if (!(this.itemTop[i+1])||(this.scrollY<this.itemTop[i+1]&&this.scrollY>=this.itemTop[i])){
            return i;
          }
        }
        return 0;
      },
    },

    created:function () {
      this.$nextTick( () => {
        this._initScroll();
        this._calculateTop();
      });
    },

    methods:{
      _initScroll:function () {
        this.foodsScroll=new BScroll(this.$refs.content,{probeType:3,click:true});
        this.foodsScroll.on('scroll',(pos)=>{
          this.scrollY=Math.abs(Math.round(pos.y));
          bus.$emit('currentIndex',this.currentIndex);
        });
      },
      _calculateTop:function () {
        let itemList=this.$refs.content.getElementsByClassName('menu-classify-hook');
        for(let i=0;i<itemList.length;i++){
          this.itemTop.push(itemList[i].offsetTop);
        }
      },
    },
    mounted:function () {
      let itemList=this.$refs.content.getElementsByClassName('menu-classify-hook');
      bus.$on('menuSelect',(index) => {
        let el = itemList[index];
        this.foodsScroll.scrollToElement(el,300);
      });
    }
  }
</script>
<style scoped>
  .content{
    height: 100px;
  }
  .content .menu-classify .title{
    box-sizing: border-box;
    height: 26px;
    border-left: 2px solid #d9dde1;
    background-color: #f3f5f7;
    font-size: 12px;
    color: rgb(147,153,159);
    line-height: 26px;
  }
  .content .menu-classify>ul {
    padding: 18px;
  }
  .content .menu-classify>ul .food{
    font-size: 0;
    display: flex;
    flex-wrap: wrap;
  }
  .content .menu-classify>ul .line{
    display: block;
    flex-shrink: 0;
    width: 100%;
    margin-top: 18px;
    margin-bottom: 18px;
    height: 1px;
    background-color: rgba(7, 17, 27, 0.1);
    transform: scaleY(0.5);
  }
  .content .menu-classify>ul .food:last-child .line{
    display: none;
  }
  .content .menu-classify>ul .food .food-icon{
    width: 57px;
    height: 57px;
    flex: 0 1;
  }
  .content .menu-classify>ul .food .food-details{
    flex: 1 0;
    position: relative;
    margin-left: 10px;
    margin-top: 2px;
  }
  .content .menu-classify>ul .food .food-details .description{
    margin-bottom: 8px;
    font-size: 10px;
    line-height: 10px;
    color: rgb(147, 153, 159);
  }
  .content .menu-classify>ul .food-title{
    font-size: 14px;
    line-height:14px;
    color: rgb(7, 17, 27);
    margin-bottom: 8px;
  }
  .content .menu-classify>ul .sales-volume,.content .menu-classify>ul .applause-rate{
    display: inline-block;
    margin-right: 12px;
    font-size: 10px;
    line-height: 10px;
    color: rgb(147, 153, 159);
  }
  .content .menu-classify>ul .food .price{
    margin-top: 8px;
    position: relative;
    display: flex;
    justify-content: space-between;
  }
  .content .menu-classify>ul .food .current-price {
    display: inline-block;
    margin-right: 8px;
    font-size:14px;
    color: rgb(255,0,0);
    font-weight: 700;
    line-height: 24px;
  }
  .content .menu-classify>ul .food .current-price .symbol{
    font-size: 10px;
    font-weight: normal;
  }
  .content .menu-classify>ul .food .old-price{
    display: inline-block;
    font-size: 10px;
    text-decoration: line-through;
    color: rgb(147, 153, 159);
    font-weight: 700;
    line-height: 48px;
  }
  .content .menu-classify>ul .food .old-price .symbol{
    font-weight: normal;
  }
  .clearfix::after{
    content: '';
    display: block;
    height: 0;
    visibility: hidden;
    clear: both;
  }
  .content .menu-classify>ul .cartButton{
    display: flex;
  }


</style>



